import React, { useState } from 'react'
import useMediaQuery from '../hooks/useMediaQuery';
import AnchorLink from 'react-anchor-link-smooth-scroll';
import menuicon from "../assets/menu-icon.svg"
import closeicon from "../assets/close-icon.svg"

 const Link = ({page, selectedPage,setSelectedPage}) =>{
    const lowerCasePage= page.toLowerCase();
    return (
        <AnchorLink 
            className={`${selectedPage===lowerCasePage ? "text-yellow":"" }
            hover:text-grey transition duration-500`} 
            href={`#${lowerCasePage}`}
            onClick={()=>setSelectedPage(lowerCasePage)}
        >
        {page}
        </AnchorLink>
    )
}

export const Navbar = ({isTopOfPage,selectedPage, setSelectedPage}) => {

    const [isMenuToggled, setIsMenuToggled] = useState(false);
    const isAboveSmallScreens = useMediaQuery("(min-width:768px)");
    const navbarBackground = isTopOfPage? "" : "bg-blue";

  return (
    <nav className={` ${navbarBackground} z-40 w-full fixed top-0 py-6`}>
         <div className="flex items-center justify-between mx-auto w-5/6 ">
            <h4 className=' font-playfair text-3xl font-bold italic'>DR</h4>

            {/* DESKTOP NAV */}

            {isAboveSmallScreens ?  (
                <div className='flex justify-between gap-16 font-opensans text-sm font-semibold'>
                    <Link 
                        page="Home"
                        selectedPage={selectedPage}
                        setSelectedPage={setSelectedPage}
                    />
                    <Link 
                        page="Skills"
                        selectedPage={selectedPage}
                        setSelectedPage={setSelectedPage}
                    />
                    <Link 
                        page="Projects"
                        selectedPage={selectedPage}
                        setSelectedPage={setSelectedPage}
                    />
                    <Link 
                        page="Education"
                        selectedPage={selectedPage}
                        setSelectedPage={setSelectedPage}
                    />
                    <Link 
                        page="Contact"
                        selectedPage={selectedPage}
                        setSelectedPage={setSelectedPage}
                    />

                </div>
            ) : (
                <button className=' rounded-full bg-blue p-2' onClick={()=>setIsMenuToggled(!isMenuToggled)}>
                    <img src={menuicon} alt="menu-icon" />
                </button>
            ) }
            {/* MOBILE MENU POPUP */}

            {!isAboveSmallScreens && isMenuToggled && (
                <div className='fixed right-0 bottom-0 h-full bg-blue w-[300px]'>
                    {/* close icon */}
                    <div className='flex justify-end p-12'>
                        <button onClick={()=> setIsMenuToggled(!isMenuToggled)}>
                            <img src={closeicon} alt="close-icon" />
                        </button>
                    </div>
                    {/* menu items */}
                    <div className='flex flex-col gap-10 ml-[33%] text-2xl text-deep-blue'>
                    <Link 
                        page="Home"
                        selectedPage={selectedPage}
                        setSelectedPage={setSelectedPage}
                    />
                    <Link 
                        page="Skills"
                        selectedPage={selectedPage}
                        setSelectedPage={setSelectedPage}
                    />
                    <Link 
                        page="Projects"
                        selectedPage={selectedPage}
                        setSelectedPage={setSelectedPage}
                    />
                    <Link 
                        page="Education"
                        selectedPage={selectedPage}
                        setSelectedPage={setSelectedPage}
                    />
                    <Link 
                        page="Contact"
                        selectedPage={selectedPage}
                        setSelectedPage={setSelectedPage}
                    />
                    </div>
                </div>
            )}

         </div>
    </nav>
  )
}
